import * as React from 'react'
import { Link, withPrefix } from 'gatsby'
import {
  container,
  heading,
  navLinks,
  navLinkItem,
  navLinkText, 
} from './layout.module.css'

const Layout = ({ pageTitle, children }) => {
  return (
    <div className={container}>
      <nav>
        <ul className={navLinks}>
          <li className={navLinkItem}>
            <Link to={withPrefix("/")} className={navLinkText}>
              Home
            </Link>
          </li>
          <li className={navLinkItem}>
            <Link to={withPrefix("/ideas")} className={navLinkText}>
              View All
            </Link>
          </li>
          <li className={navLinkItem}>
            <Link to={withPrefix("/search")} className={navLinkText}>
              Search
            </Link>
          </li>
          <li className={navLinkItem}>
            <Link to={withPrefix("/random")} className={navLinkText}>
              Random
            </Link>
          </li>
          <li className={navLinkItem}>
            <Link to={withPrefix("/contact-me")} className={navLinkText}>
              Contact Me
            </Link>
          </li>
        </ul>
      </nav>
      <main>
        <h1 className={heading}>{pageTitle}</h1>
        {children}
      </main>
      
    </div>
  )
}

export default Layout